<template>
  <div class="foodPractice">
    <foodHeaderNav title="购物车" eventShow="true" @hidePageFun="gotoBack()"></foodHeaderNav>
    <div class="title">烧法</div>
    <div class="body">
      <div class="left-container">
        <div class="vegetables-list">
          <div
            class="vegetables-item"
            v-for="(item,index) in menuItem.itemClasslist"
            :key="index"
            @click="menuListFun(index)"
            :class="[selecteDish == index?'vegetables-item-active':'']"
          >
            <div class="vegetables-item-icon"></div>
            <div class="vegetables-item-text">{{item.itemClassName}}</div>
          </div>
          <!-- <div class="vegetables-item">
            <div class="vegetables-item-icon"></div>
            <div class="vegetables-item-text">什么类大家发挥科技阿萨德黄飞鸿</div>
          </div>
          <div class="vegetables-item vegetables-item-active">
            <div class="vegetables-item-icon"></div>
            <div class="vegetables-item-text">什么类大家发挥科技阿萨德黄飞鸿</div>
          </div>
          <div class="vegetables-item">
            <div class="vegetables-item-icon"></div>
            <div class="vegetables-item-text">什么类</div>
          </div>-->
        </div>
      </div>
      <div class="right-container">
        <div class="dish-name-list" v-for="(item, index) in menuItem.itemClasslist" :key="index">
          <div v-if="selecteDish == index">
            <div
              class="dish-name-item"
              v-for="(dish, i) in item.pluCondgplist"
              :key="i"
              @click="sideDishFun(i)"
            >
              <div class="dish-name-left">{{dish.descript}}</div>
              <div class="dish-name-right" v-if="dish.chose">
                <i class="iconfont icon-right"></i>
              </div>
            </div>
          </div>
        </div>
        <!-- <div class="dish-name-list">
          <div class="dish-name-item">
            <div class="dish-name-left">红烧</div>
            <div class="dish-name-right"></div>
          </div>
          <div class="dish-name-item">
            <div class="dish-name-left">红烧</div>
            <div class="dish-name-right">
              <i class="iconfont icon-right"></i>
            </div>
          </div>
          <div class="dish-name-item">
            <div class="dish-name-left">红烧</div>
            <div class="dish-name-right"></div>
          </div>
        </div>-->
      </div>
    </div>
    <div class="remarks" ref="remarks" contenteditable="true">备注：
      <!-- <textarea name id cols="30" rows="10">备注：</textarea> -->
    </div>
    <div class="foot">
      <div class="cancel foot-btn" @click="gotoBack()">取 消</div>
      <div class="confirm foot-btn" @click="okFun()">开 单</div>
    </div>
  </div>
</template>
<script>
import foodHeaderNav from "../common/foodHeaderNav";
export default {
  components: {
    foodHeaderNav
  },
  props: {
    dishItem: Object,
    dishIndex: Number
  },
  data() {
    return {
      selecteDish: 0,
      menuItem: this.dishItem
    };
  },
  methods: {
    gotoBack() {
      this.$emit("hidePageFun");
    },
    menuListFun(index) {
      this.selecteDish = index;
    },
    sideDishFun(i) {
      this.menuItem.itemClasslist[this.selecteDish].pluCondgplist.forEach(
        (element, index) => {
          if (i == index) {
            element.chose = true;
          } else {
            element.chose = false;
          }
        }
      );
    },
    okFun() {
      this.menuItem.remarks = this.$refs.remarks.innerHTML;
      this.$store.commit("changeMenu", {
        item: this.menuItem,
        index: this.dishIndex
      });
      this.$emit("hidePageFun");
    }
  }
};
</script>
<style lang="less" scoped>
.foodPractice {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 9;
  width: 100%;
  height: 100%;
  background-color: #f6f8f9;

  .title {
    color: #fff;
    height: 0.9rem;
    line-height: 0.9rem;
    background-color: #737b8a;
  }
  .body {
    position: absolute;
    top: 2.037rem;
    bottom: 4.53rem;
    width: 100%;
    display: flex;
    padding-top: 0.133rem;
    .left-container {
      width: 2.587rem;
      height: 100%;
      margin-right: 4px;
      background-color: #fff;
      overflow: auto;
      .vegetables-item {
        height: 1.067rem;
        font-size: 0.373rem;
        color: #393c41;
        display: flex;
        .vegetables-item-icon {
          width: 3px;
          height: 100%;
          margin-right: 0.213rem;
          background-color: #fff;
        }
        .vegetables-item-text {
          flex: 1;
          text-align: left;
          line-height: 1.067rem;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
      }

      .vegetables-item-active {
        background-color: #f6f8f9;
        .vegetables-item-icon {
          background-color: #f69c2d;
        }
      }
    }
    .right-container {
      flex: 1;
      height: 100%;
      overflow: auto;
      .dish-name-list {
        .dish-name-item {
          display: flex;
          justify-content: space-between;
          align-items: center;
          background-color: #fff;
          margin-bottom: 0.133rem;
          padding-left: 0.427rem;
          padding-right: 0.48rem;
          height: 1.067rem;
          border-radius: 0.133rem;
          .dish-name-left {
            font-size: 0.427rem;
            color: #333333;
          }
          .dish-name-right {
            .iconfont {
              color: #f89635;
              font-size: 0.48rem;
            }
          }
        }
      }
    }
  }
  .remarks {
    position: fixed;
    bottom: 1.33rem;
    width: 100%;
    text-align: left;
    height: 120px;
    border-top: 1px solid #e9ecf1;
    border-bottom: 1px solid #e9ecf1;
    outline: none;
    font-size: 0.373rem;
    color: #a2a8bd;
    padding: 0.213rem 0.427rem;
    box-sizing: border-box;
  }
  .foot {
    position: fixed;
    bottom: 0px;
    background-color: #fff;

    width: 100%;
    display: flex;

    .foot-btn {
      flex: 1;
      color: #ffffff;
      text-align: center;
      padding: 0.347rem 0;
      font-size: 0.427rem;
    }
    .cancel {
      color: #333333;
    }
    .confirm {
      color: #ffffff;
      background-image: linear-gradient(-90deg, #f6a129 0%, #ff6e61 98%);
    }
  }
}
</style>



