<template>
  <div class="header">
    <div v-if="eventShow=='true'" class="iconfont-container" @click="hideFun()">
      <span class="iconfont icon-jiantouzuo"></span>
      <span v-if="iconText" class="iconfont-text">{{iconText}}</span>
    </div>

    <p>{{title}}</p>
  </div>
</template>
<script>
export default {
  props: {
    iconText: String,
    title: String,
    eventShow: String
  },
  methods: {
    hideFun() {
      this.$emit("hidePageFun");
    }
  }
};
</script>
<style lang="less" scoped>
.header {
  width: 100%;
  height: 1.28rem;
  line-height: 1.28rem;
  background: #393c41;
  font-size: 0.427rem;
  color: #ffffff;
  text-align: center;
  position: relative;
  .iconfont-container {
    position: absolute;
    top: 0px;
    left: 0.4rem;
    font-size: 0.533rem;
    .iconfont-text {
      font-size: 0.427rem;
    }
  }
}
</style>



