<template>
  <div class="shopping-cart">
    <foodHeaderNav title="购物车" eventShow="true" @hidePageFun="gotoBack()"></foodHeaderNav>
    <div class="body">
      <div class="menu-list">
        <div class="menu-item" v-for="(item, index) in MenuArr" :key="index">
          <div class="menu-item-content">
            <div class="menu-name">{{item.pluDescript}}</div>

            <div class="menu-practice">
              <span v-for="(practice,i) in item.itemClasslist" :key="i">
                <span v-for="(tips, j) in practice.pluCondgplist" :key="j">
                  <span class="menu-practice-chose" v-if="tips.chose">{{tips.descript}}</span>
                </span>
              </span>
            </div>
          </div>
          <div class="menu-price">{{item.pluPrice}}</div>
          <div class="menu-item-operating">
            <div class="cut-btn" @click="cutNumFun(item,index)">
              <i class="iconfont icon-jian"></i>
            </div>
            <span class="num">{{item.num}}</span>
            <div class="add-btn icon-btn" @click="addNumFun(item,index)">
              <i class="iconfont icon-jia1"></i>
            </div>
          </div>
          <i
            class="menu-item-modify iconfont icon-jiantou1"
            @click="modifyFun(item,index)"
            v-if="item.itemClasslist.length>0"
          ></i>
          <i class="menu-item-Station" v-show="item.itemClasslist.length<1"></i>
        </div>
      </div>
    </div>
    <div class="foot">
      <div class="total-price">
        <span class="total-price-tips">总计：</span>
        <span class="total-price-num">
          <span class="total-price-unit">￥</span>
          {{totalPrice}}
        </span>
      </div>
      <div class="confirm-btn" @click="gotoOrderFun()">下单</div>
    </div>
    <foodPractice
      v-if="isShowModifyPage"
      :dishItem="dishItem"
      :dishIndex="dishIndex"
      @hidePageFun="hidePageFun"
    ></foodPractice>
  </div>
</template>
<script>
import moment from "moment";
import foodHeaderNav from "../common/foodHeaderNav";
import foodPractice from "../common/foodPractice";
import { mapState, mapGetters } from "vuex";
import { Indicator, Toast } from "mint-ui";
export default {
  components: {
    foodHeaderNav,
    foodPractice
  },
  data() {
    return {
      cartTotalNum: 0,
      isShowModifyPage: false,
      dishItem: {},
      dishIndex: null,
      newMenuArr: [],
      isNowOrder: false
    };
  },
  computed: {
    ...mapState({
      // MenuArr: state => state.food.MenuArr,
      CreateOrderInfo: state => state.food.CreateOrderInfo,
      seatsInfo: state => state.food.seatsInfo,
      orderNum: state => state.food.orderNum
    }),
    ...mapGetters({
      MenuArr: "getMenuArr",
      totalPrice: "getTotalPrice"
    })
  },
  mounted() {},
  methods: {
    gotoBack() {
      this.$router.push("/foodMenu");
    },
    hidePageFun() {
      this.isShowModifyPage = false;
    },
    cutNumFun(item, index) {
      if (this.MenuArr[index].num > 0) {
        this.MenuArr[index].num--;
        this.$store.commit("changeMenu", { item: item, index: index });
      }
    },
    addNumFun(item, index) {
      this.MenuArr[index].num++;
      this.$store.commit("changeMenu", { item: item, index: index });
    },
    modifyFun(item, index) {
      this.dishItem = item;
      this.dishIndex = index;
      this.isShowModifyPage = true;
    },
    gotoOrderFun() {
      console.log("CreateOrderInfo:", this.CreateOrderInfo);
      console.log("seatsInfo:", this.seatsInfo);
      let dinnerTime = "";
      if (this.CreateOrderInfo.date) {
        dinnerTime =
          this.CreateOrderInfo.date + " " + this.CreateOrderInfo.time;
        this.isNowOrder = false;
      } else {
        dinnerTime = moment().format("YYYY-MM-DD HH:MM");
        this.isNowOrder = true;
      }
      let menuItems = {
        pccode: this.seatsInfo.pccode,
        account: this.totalPrice,
        plus: []
      };
      console.log("this.MenuArr:", this.MenuArr);
      this.MenuArr.forEach(element => {
        console.log("element.plucode:", element.pluCode);
        let item = {
          sortCode: element.sortCode,
          plucode: element.pluCode,
          descript: element.pluDescript,
          descript_en: element.pluDescriptEn,
          unit: "",
          pluPrice: element.pluPrice,
          pluNumber: element.num,
          comboMeals: []
        };
        if (element.itemClasslist.length > 0) {
          let comboMeals = [];
          element.itemClasslist.forEach(ele => {
            let Meals = {};
            ele.pluCondgplist.forEach(Condg => {
              if (Condg.chose) {
                Meals = {
                  code: Condg.code,
                  pluName: Condg.descript,
                  pluNameEn: Condg.descriptEn,
                  price: Condg.price
                };
              }
            });
            comboMeals.push(Meals);
          });
          item["comboMeals"] = comboMeals;
        }
        menuItems.plus.push(item);
      });
      if (this.orderNum) {
        this.$http
          .get("/ohotel/router", {
            params: {
              method: "updateMasterItem",
              accnt: this.orderNum,
              menuItems: menuItems
            },
            vv: 5
          })
          .then(res => {
            if (res.data.code == "0") {
              this.$store.commit("clearCart");
              this.$router.push("/foodOrder");
            } else {
              this.msg(res.data.msg);
            }
          });
      } else {
        this.$http
          .get("/ohotel/router", {
            params: {
              method: "saveOrderRequest",
              v: "2.0",
              pccode: this.seatsInfo.pccode,
              dinnerTime: dinnerTime,
              gsts: this.CreateOrderInfo.peopleNum,
              waiterCode: this.CreateOrderInfo.waiter,
              tableNo: this.seatsInfo.code,
              amount: this.totalPrice,
              menuItems: menuItems
            },
            vv: 5
          })
          .then(res => {
            if (res.data.code == "0") {
              this.$store.commit("clearCart");
              this.$store.commit("submitOrderNum", {
                from: "shoppingCart",
                orderNum: res.data.data
              });
              if (!this.isNowOrder) {
                let eatTime = localStorage.eatTime;
                if (eatTime) {
                  eatTime = JSON.parse(eatTime);
                }
                if (
                  eatTime &&
                  eatTime.hasOwnProperty("s" + this.seatsInfo.pccode)
                ) {
                  let idIn = false;
                  eatTime["s" + this.seatsInfo.pccode].forEach(ele => {
                    if (ele.table == this.seatsInfo.code) {
                      ele.time = this.CreateOrderInfo.time;
                      idIn = true;
                    }
                  });
                  if (!idIn) {
                    eatTime["s" + this.seatsInfo.pccode].push({
                      table: this.seatsInfo.code,
                      time: this.CreateOrderInfo.time
                    });
                  }
                  localStorage.eatTime = JSON.stringify(eatTime);
                } else {
                  let eatTimeObj = localStorage.eatTime;
                  if (eatTimeObj) {
                    eatTimeObj = JSON.parse(localStorage.eatTime);
                  } else {
                    eatTimeObj = {};
                  }
                  let eatTimeArr = [
                    {
                      table: this.seatsInfo.code,
                      time: this.CreateOrderInfo.time
                    }
                  ];
                  eatTimeObj["s" + this.seatsInfo.pccode] = eatTimeArr;
                  localStorage.eatTime = JSON.stringify(eatTimeObj);
                }
              }

              this.$router.push("/foodOrder");
            } else {
              this.msg(res.data.msg);
            }
          });
      }
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "bottom",
        duration: 3000
      });
    }
  }
};
</script>
<style lang="less" scoped>
.shopping-cart {
  .menu-list {
    padding: 0 0.133rem;
    .menu-item {
      background-color: #ffffff;
      border-radius: 0.133rem;
      display: flex;
      align-items: center;
      padding: 0.133rem 0.32rem;
      margin-top: 0.133rem;
      .menu-item-content {
        flex: 1;
        text-align: left;
        .menu-name {
          font-size: 0.427rem;
          color: #333333;
        }
        .menu-practice {
          font-size: 0.32rem;
          color: #f7544d;
          .menu-practice-chose {
            margin-right: 0.133rem;
          }
        }
      }
      .menu-price {
        margin-right: 1.33rem;
        font-size: 0.373rem;
      }
      .menu-item-operating {
        width: 2.4rem;
        display: flex;
        align-items: center;
        .cut-btn {
          .iconfont {
            font-weight: bold;
            color: #ff7b02;
            font-size: 0.48rem;
          }
        }
        .num {
          font-size: 19.2px;
          color: #77a0ff;
          width: 0.933rem;
        }
        .add-btn {
          width: 0.507rem;
          height: 0.507rem;
          line-height: 0.507rem;
          border-radius: 2.667rem;
          background: #ff7b02;
          .iconfont {
            color: #fff;
            font-size: 0.32rem;
          }
        }
      }
      .menu-item-modify {
      }
      .menu-item-Station {
        display: inline-block;
        width: 0.427rem;
        height: 1px;
      }
    }
  }
  .foot {
    position: fixed;
    bottom: 0px;
    height: 1.33rem;
    width: 100%;
    display: flex;
    background-color: #fff;
    border-top: 1px solid #e9ecf1;
    .total-price {
      flex: 1;
      margin-left: 0.507rem;
      height: 100%;
      display: flex;
      align-items: center;
      .total-price-tips {
        font-size: 0.32rem;
        color: #a2a8bd;
      }

      .total-price-num {
        font-size: 0.64rem;
        color: #f7544d;
        .total-price-unit {
          font-size: 0.32rem;
        }
      }
    }
    .confirm-btn {
      height: 100%;
      line-height: 3;
      width: 120px;
      font-size: 0.427rem;
      color: #ffffff;
      background-image: linear-gradient(-90deg, #f6a129 0%, #ff6e61 98%);
    }
  }
}
</style>



